
import { observable, reaction, computed, autorun } from 'mobx'

// Observable State 
// Mobx       .
const calculator = observable({
    a: 1,
    b: 2
})


//      
reaction(
    () => calculator.a, (value, reaction) => {
        console.log(`a ${value}  !`)
    }
)


reaction(
    () => calculator.b,
    (value) => {
        console.log(`b ${value}  !`)
    }
)


calculator.a = 10
calculator.b = 20


// computed   
const sum = computed(() => {
    console.log('!')
    return calculator.a + calculator.b
})

sum.observe(() => calculator.a)
sum.observe(() => calculator.b)

calculator.a = 20
calculator.b = 30

//      .
console.log(sum.value)
console.log(sum.value)
//      .
console.log(sum.value);
console.log(sum.value);

//      
calculator.a = 20
console.log(sum.value)



// computed   
const minus = computed(() => {
    console.log(' ')
    return calculator.a - calculator.b
})

// autorun reaction computed observe     . autorun        .
//  computed   .get()   observe()  .
autorun(() => console.log(`@@@ a  ${calculator.a}  !`))
autorun(() => console.log(`@@@ b  ${calculator.b}  !`))
autorun(() => minus.get())

calculator.a = 11
calculator.b = 12


//   
console.log(minus.value);
console.log(minus.value);

calculator.a = 20;

//    .
console.log(minus.value);